@import url(https://fonts.googleapis.com/css?family=Roboto:300);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.login-page {
  width: 360px;
  padding: 8% 0 0;
  margin: auto;
}
.form {
  position: relative;
  z-index: 1;
  background: #FFFFFF;
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.form input {
  font-family: "Roboto", sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}
.form button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #2a3f54;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #FFFFFF;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
.form button:hover,.form button:active,.form button:focus {
  background: #43A047;
}
.form .message {
  margin: 15px 0 0;
  color: #b3b3b3;
  font-size: 12px;
}
.form .message a {
  color: #4CAF50;
  text-decoration: none;
}
.form .register-form {
  display: none;
}
.container {
  position: relative;
  z-index: 1;
  max-width: 300px;
  margin: 0 auto;
}
.container:before, .container:after {
  content: "";
  display: block;
  clear: both;
}
.container .info {
  margin: 50px auto;
  text-align: center;
}
.container .info h1 {
  margin: 0 0 15px;
  padding: 0;
  font-size: 36px;
  font-weight: 300;
  color: #1a1a1a;
}
.container .info span {
  color: #4d4d4d;
  font-size: 12px;
}
.container .info span a {
  color: #000000;
  text-decoration: none;
}
.container .info span .fa {
  color: #EF3B3A;
}
.login {
  background: #f9fafa; /* fallback for old browsers */
  background: -webkit-linear-gradient(right, #939394, #5985b1);
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;      
  min-height: 42.5em
}

body {
  background-color: #dedede;
  width: 100%;
  word-wrap: break-word;
  word-break: break-all;
}

.topbar {
background: #2A3F54;
border-color: #2A3F54;
border-radius: 0px;
}

.topbar .navbar-header a {
color: #ffffff;
}

.wrapper {
  padding-left: 0px;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.sidebar {
  z-index: 1000;
  position: fixed;
  top: 50px;
  left: -50px;
  width: 50px;
  height: 100%;
  overflow-y: auto;
  background: #2A3F54;
color: #ffffff;
-webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.main {
width: 100%;
  position: relative;
  padding-bottom:20px;
}

.wrapper.toggled {
padding-left: 50px;
}

.wrapper.toggled .sidebar {
left: 0;
}

/* Sidebar Styles */

.sidebar-nav {
  position: absolute;
  top: 52px;
  width: 50px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.sidebar-nav li {
  line-height: 40px;
}
.sidebar-nav li a {
  display: block;
  text-decoration: none;
  color: #e8e8e8;
  padding: 0;
  text-align:center;
}

.sidebar-nav li a:hover, .sidebar-nav li.active a {
  text-decoration: none;
  color: #fff;
  background: #fff;
  background: rgba(255,255,255,0.2);
}

.sidebar-nav li a:active,
.sidebar-nav li a:focus {
  text-decoration: none;
}

.sidebar-nav li span, .subbar li span {
display : none;
}

nav.subbar {
position: relative;
width: 100%;
border-radius: 0px;
background: #fff;
margin: 50px 0 -50px 0;
padding: 10px 0 0 0;
z-index: 2;
}
nav.subbar > ul.nav.nav-tabs {
padding: 0 5px;
}

nav.subbar > ul.nav.nav-tabs > li.active > a {
  background: #dedede;
  border-top: 1px solid #a6a6a6;
  border-left: 1px solid #a6a6a6;
  border-right: 1px solid #a6a6a6;
  border-radius: 0px;
}

.content {
  margin-top: 70px;
  padding: 0 30px;
}

@media(min-width:768px){
.subbar li span {
  display: inline;
}
}

@media(min-width:992px) {
  .wrapper {
    padding-left: 50px;
  }

  .sidebar {
    left: 0;
    width: 50px;
}

.wrapper.toggled {
  padding-left: 200px;
}

.wrapper.toggled .sidebar, .wrapper.toggled .sidebar-nav {
  width: 200px;
}

.wrapper.toggled .sidebar-nav li a {
  text-align: left;
  padding: 0 0 0 10px;
}

.wrapper.toggled .sidebar-nav li span {
  display: inline;
}

}

.navbar-btn {
  background: none;
  border: none;
  height: 35px;
  min-width: 35px;
  color: #fff;
}
.navbar-text {
margin-top: 14px;
margin-bottom: 14px;
}
@media (min-width: 768px) {
.navbar-text {
  float: left;
  margin-left: 15px;
  margin-right: 15px;
}
}

.paddintop{
  padding-top: 20pt;
  margin-top: 3em
}

.koko{
  background: #dedede;
}

.snip1396 {
  font-family: 'Raleway', Arial, sans-serif;
  position: relative;
  overflow: hidden;
  margin: 10px;
  min-width: 230px;
  max-width: 315px;
  width: 100%;
  background: #20638f;
  text-align: left;
  color: #ffffff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
  font-size: 16px;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}
.snip1396 * {
  box-sizing: border-box;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
.snip1396 > img,
.snip1396 .image img {
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
  max-width: 100%;
}
.snip1396 > img {
  vertical-align: top;
  position: relative;
  -webkit-filter: blur(5px);
  filter: blur(5px);
  opacity: 0.6;
}
.snip1396 figcaption,
.snip1396 .image {
  -webkit-transition-delay: 0.2s;
  transition-delay: 0.2s;
}
.snip1396 .image {
  position: absolute;
  top: 0;
  bottom: 45%;
  right: 0;
  left: 0;
  overflow: hidden;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.1), 0px 1px 0 rgba(255, 255, 255, 0.2);
}
.snip1396 .image img {
  position: absolute;
  top: 0;
}
.snip1396 figcaption {
  position: absolute;
  top: 55%;
  bottom: 46px;
  left: 20px;
  right: 20px;
  border-bottom: 2px solid #ffffff;
  padding-top: 20px;
  z-index: 1;
}
.snip1396 h3,
.snip1396 p {
  margin: 0;
}
.snip1396 h3 {
  font-weight: 700;
  margin-bottom: 5px;
  text-transform: uppercase;
}
.snip1396 p {
  font-size: 0.9em;
  letter-spacing: 1px;
  font-weight: 400;
}
.snip1396 .price,
.snip1396 .add-to-cart {
  position: absolute;
  bottom: 0;
  padding: 0 20px;
  font-weight: 500;
  line-height: 48px;
  letter-spacing: 1px;
}
.snip1396 .price {
  left: 0;
}
.snip1396 .price s {
  margin-right: 5px;
  opacity: 0.5;
  font-size: 0.9em;
}
.snip1396 .add-to-cart {
  -webkit-transform: translateX(-20px);
  transform: translateX(-20px);
  opacity: 0;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 0.9em;
  right: 0;
}
.snip1396 .add-to-cart i {
  margin-left: 5px;
  font-size: 1.1em;
}
.lds-roller {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.lds-roller div {
  -webkit-animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  -webkit-transform-origin: 32px 32px;
          transform-origin: 32px 32px;
}
.lds-roller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #fff;
  margin: -3px 0 0 -3px;
}
.lds-roller div:nth-child(1) {
  -webkit-animation-delay: -0.036s;
          animation-delay: -0.036s;
}
.lds-roller div:nth-child(1):after {
  top: 50px;
  left: 50px;
}
.lds-roller div:nth-child(2) {
  -webkit-animation-delay: -0.072s;
          animation-delay: -0.072s;
}
.lds-roller div:nth-child(2):after {
  top: 54px;
  left: 45px;
}
.lds-roller div:nth-child(3) {
  -webkit-animation-delay: -0.108s;
          animation-delay: -0.108s;
}
.lds-roller div:nth-child(3):after {
  top: 57px;
  left: 39px;
}
.lds-roller div:nth-child(4) {
  -webkit-animation-delay: -0.144s;
          animation-delay: -0.144s;
}
.lds-roller div:nth-child(4):after {
  top: 58px;
  left: 32px;
}
.lds-roller div:nth-child(5) {
  -webkit-animation-delay: -0.18s;
          animation-delay: -0.18s;
}
.lds-roller div:nth-child(5):after {
  top: 57px;
  left: 25px;
}
.lds-roller div:nth-child(6) {
  -webkit-animation-delay: -0.216s;
          animation-delay: -0.216s;
}
.lds-roller div:nth-child(6):after {
  top: 54px;
  left: 19px;
}
.lds-roller div:nth-child(7) {
  -webkit-animation-delay: -0.252s;
          animation-delay: -0.252s;
}
.lds-roller div:nth-child(7):after {
  top: 50px;
  left: 14px;
}
.lds-roller div:nth-child(8) {
  -webkit-animation-delay: -0.288s;
          animation-delay: -0.288s;
}
.lds-roller div:nth-child(8):after {
  top: 45px;
  left: 10px;
}
@-webkit-keyframes lds-roller {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-roller {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

